<template>
	<view class="">
		<!-- 手术设计介绍 -->
		<view class="introduce">
			<view class="example-body">
				<text>手术设计介绍</text>
			</view>
			<view class="content">
				<view class="text1">
					<text>病种类别：</text>
					<text v-if="arrData.diseaseCategoryName">{{arrData.diseaseCategoryName||'暂无'}}</text>
					<text v-else>暂无数据</text>
				</view>
				<view class="text1">
					<text>材料应用：</text>
					<text v-if="arrData.materials">{{arrData.materials.name||'暂无'}}</text>
					<text v-else>暂无数据</text>
				</view>
				<view class="text1">
					<text>方法说明：</text>
					<text v-if="arrData.title">{{arrData.title||'暂无数据'}}</text>
					<text v-else>暂无数据</text>
				</view>
			</view>
		</view>
		<!--  数字化3D打印导板-->
		<view class="introduce">
			<view class="example-body">
				<text>数字化3D打印导板</text>
			</view>
			<view class="content">
				<view v-if="arrData.threeDGuideList" class="" v-for="(item,index) in arrData.threeDGuideList" :key='index'>
					<view class="title">
						<view class="indexDot"></view>
						<view class="number">
							<text>{{index+1}}</text>
							<text>. {{item.name}}</text>
						</view>
					</view>
					<view class="imgpic">
						<view class="imgitems" v-for="(itemImage,index1) in item.previewImage" :key='index1'>
							<u-image height="100%" :src="itemImage" mode="aspectFit" @tap="PreviewPic(index1,item.previewImage)">
								<uni-icons type="spinner-cycle" slot="loading"></uni-icons>
								<view slot="error" style="font-size: 24rpx;">加载失败</view>
							</u-image>
						</view>
					</view>
				</view>
				<view class="" v-else>
					暂无数据
				</view>
			</view>
		</view>
		<!-- 数字化3d打印器械 -->
		<view class="introduce">
			<view class="example-body">
				<text>数字化3D打印器械</text>
			</view>
			<view class="content">
				<view v-if="arrData.threeDGuideList" class="" v-for="(item,index) in arrData.threeDInstrumentList" :key='index'>
					<view class="title">
						<view class="indexDot"></view>
						<view class="number">
							<text>{{index+1}}</text>
							<text>. {{item.name}}</text>
						</view>
					</view>
					<view class="imgpic">
						<view class="imgitems" v-for="(itemImage,index1) in item.previewImage" :key='index1'>
							<u-image height="100%" :src="itemImage" mode="aspectFit" @tap="PreviewPic(index1,item.previewImage)">
								<uni-icons type="spinner-cycle" slot="loading"></uni-icons>
								<view slot="error" style="font-size: 24rpx;">加载失败</view>
							</u-image>
						</view>
					</view>
				</view>
				<view class="" v-else>
					暂无数据
				</view>
			</view>
		</view>
		<!-- 手术方法步骤 -->
		<view class="introduce">
			<view class="example-body">
				<text>手术方法步骤</text>
			</view>
			<view class="content">
				<view v-if="arrData.threeDGuideList" class="" v-for="(item,index) in arrData.surgicalDesignStepsList" :key='index'>
					<view class="title">
						<view class="indexDot"></view>
						<view class="number">
							<text>{{index+1}}</text>
							<text>. {{item.name}}</text>
						</view>
					</view>
					<view class="imgpic">
						<view class="imgitems" v-for="(itemImage,index1) in item.previewImage" :key='index1'>
							<u-image height="100%" :src="itemImage" mode="aspectFit" @tap="PreviewPic(index1,item.previewImage)">
								<uni-icons type="spinner-cycle" slot="loading"></uni-icons>
								<view slot="error" style="font-size: 24rpx;">加载失败</view>
							</u-image>
						</view>
					</view>
				</view>
				<view class="" v-else>
					暂无数据
				</view>
			</view>
		</view>
		<!-- 术前情况 -->
		<view class="introduce">
			<view class="example-body">
				<text>术前情况</text>
			</view>
			<view class="content">
				<view v-if="arrData.threeDGuideList" class="" v-for="(item,index) in arrData.preoperativeConditionList" :key='index'>
					<view class="title">
						<view class="indexDot"></view>
						<view class="number">
							<text>{{index+1}}</text>
							<text>. {{item.name}}</text>
						</view>
					</view>
					<view class="imgpic">
						<view class="imgitems" v-for="(itemImage,index1) in item.previewImage" :key='index1'>
							<u-image height="100%" :src="itemImage" mode="aspectFit" @tap="PreviewPic(index1,item.previewImage)">
								<uni-icons type="spinner-cycle" slot="loading"></uni-icons>
								<view slot="error" style="font-size: 24rpx;">加载失败</view>
							</u-image>
						</view>
					</view>
				</view>
				<view class="" v-else>
					暂无数据
				</view>
			</view>
		</view>
		<!-- 术后情况 -->
		<view class="introduce">
			<view class="example-body">
				<text>术后情况</text>
			</view>
			<view class="content">
				<view v-if="arrData.threeDGuideList" class="" v-for="(item,index) in arrData.postoperativeSituationList" :key='index'>
					<view class="title">
						<view class="indexDot"></view>
						<view class="number">
							<text>{{index+1}}</text>
							<text>. {{item.name}}</text>
						</view>
					</view>
					<view class="imgpic">
						<view class="imgitems" v-for="(itemImage,index1) in item.previewImage" :key='index1'>
							<u-image height="100%" :src="itemImage" mode="aspectFit" @tap="PreviewPic(index1,item.previewImage)">
								<uni-icons type="spinner-cycle" slot="loading"></uni-icons>
								<view slot="error" style="font-size: 24rpx;">加载失败</view>
							</u-image>
						</view>
					</view>
				</view>
				<view class="" v-else>
					暂无数据
				</view>
			</view>
		</view>
		<!-- word -->
		<view class="introduce">
			<view class="example-body">
				<text>word</text>
			</view>
			<view class="content">
				<view v-if="arrData.wordPdfIds">
					<view class="tag-view" >
						<uni-tag text="点击预览word" @tap="previewword(arrData.loadLink[arrData.wordPdfIds])" />
					</view>
				</view>
				<view v-else>
					暂无数据
				</view>
			</view>
		</view>
		<!-- ppt -->
		<view class="introduce">
			<view class="example-body">
				<text>ppt</text>
			</view>
			<view class="content">
				<view v-if="arrData.pptPdfIds">
					<view class="tag-view" >
						<uni-tag  text="点击预览ppt" @tap="previewppt(arrData.loadLink[arrData.pptPdfIds])" />
					</view>
				</view>
				<view class="" v-else>
					暂无数据
				</view>
			</view>
		</view>
		<!-- 视频描述 -->
		<view class="introduce">
			<view class="example-body">
				<text>视频描述</text>
			</view>
			<view class="content" style="text-align: center;">
				<video v-if="arrData.videoModelIds" :src="arrData.loadLink[arrData.videoModelIds]" controls></video>
				<view class="" v-else>
					暂无视频数据
				</view>
			</view>
		</view>
	</view>
</template>

<script>
	export default{
		data(){
			return{
			}
		},
		props:{
			arrData:{
				type:Object,
				default:()=>{
					return{}
				}
			}
		},
		methods:{
			cancel(){
				
			},
			// 预览图片
			PreviewPic(index,data){
				console.log(index)
				console.log(data)
				console.log(Object.values(data))//将对象转为数组
				var arr=Object.values(data)
				uni.previewImage({
					current:index,//预览的第一张图，可以使index，可以是路径
					urls:arr,//必须是数组
					indicator:'number',
					loop:true,
					longPressActions:{
						 itemList: ['保存图片','分享朋友','分享朋友圈'],//长按时弹出的列表选项
						 success: (data) => {
							console.log('选中了第' + (data.tapIndex + 1) + '个按钮,第' + (data.index + 1) + '张图片');
							if(data.tapIndex==0){//选中第一个按钮
								console.log(arr[data.index])
								// 保存图片
								uni.saveImageToPhotosAlbum({
									filePath:arr[data.index],
									success(e){
										uni.showToast({
											title:'保存成功',
											icon:'none'
										})
									},
									fail(e){
										uni.showToast({
											title:e.errMsg
										})
									}
								})
							}else if(data.tapIndex==1){
								console.log(arr[data.index])
								// 分享聊天列表
								uni.share({
									provider: "weixin",
									scene: "WXSceneSession",
									type: 2,
									imageUrl: arr[data.index],
									success(res) {
										console.log("success:" + JSON.stringify(res));
									},
									fail(err) {
										console.log("fail:" + JSON.stringify(err));
									}
								})
							}else if(data.tapIndex==2){
								console.log(arr[data.index])
								// 分享朋友圈
								uni.share({
									provider: "weixin",
									scene: "WXSenceTimeline",
									type: 2,
									imageUrl: arr[data.index],
									success(res) {
										console.log("success:" + JSON.stringify(res));
									},
									fail(err) {
										console.log("fail:" + JSON.stringify(err));
									}
								})
							}
						 },
						 fail:(err)=>{
						 }
					},
					success(e){
						console.log(e)
					}
				})
			},
			// 预览word
			previewword(url){
				// console.log(encodeURIComponent(url))
				var url=encodeURIComponent(url)
				uni.navigateTo({
					url:'/pages/previewPdf/previewPdf?pdfUrl='+url,
					animationType:'pop-in'
				})
			},
			// 预览ppt
			previewppt(url){
				// console.log(encodeURIComponent(url))
				var url=encodeURIComponent(url)
				uni.navigateTo({
					url:'/pages/previewPdf/previewPdf?pdfUrl='+url,
					animationType:'pop-in'
				})
			},
		}
	}
</script>

<style scoped lang="scss">
	.introduce{
		padding:0 30rpx;
		padding-bottom: 20rpx;
		border-bottom: 1px solid #F0EFEF;
		color: #000000;
		font-size: 28rpx;
		background-color: #FFFFFF;
		margin-top: 20rpx;
		.content{
			line-height: 50rpx;
			padding-top: 10rpx;
			.title{
				display: flex;
				align-items: center;
				.indexDot{
					margin-right: 20rpx;
					width: 16rpx;
					height: 16rpx;
					background: #86B0D4;
					border-radius: 50%;
				}
				.number{
					color: #000000;font-size: 28rpx;
				}
			}
			.imgpic{
				display: flex;
				justify-content: space-around;
				flex-wrap: wrap;
				.imgitems{
					width: 210rpx;
					height: 210rpx;
					margin: 10rpx 20rpx;
					border: 1px solid #EEEEEE;
					.img{
						margin: 0 auto;
						width: 100%;
						height: 100%;
					}
				}
			}
		}
		.text1 uni-text:nth-child(1){
			color: #8F8F8F;
		}
	}
	.example-body {
		flex-direction: row;
		flex-wrap: wrap;
		font-size: 28rpx;
		background-color: #ffffff;
		color: #232323;
		font-family: PingFang-SC-Bold;
		font-weight: bold;
		padding: 20rpx 0;
		border-bottom: 1px solid #F0F0F0;
	}
	.tag-view /deep/ .uni-tag--default{
		background: #E9F3FC;
		color: #80AED5!important;
	}
</style>
